import { Form, Input, Button } from 'antd-mobile'
import NavMobile from '../../../components/mobile/Nav'
import './index.css'

const ModifyPasswordMobile = () => {
  const [form] = Form.useForm()

  const onSubmit = () => {
    const values = form.getFieldsValue()
    console.log('values', values)
  }

  const initialValues = {}

  return (
    <div className="modify-password-mobile">
      <NavMobile title="修改密码" />
      <div className="modify-password-mobile-content">
        <Form
          layout="horizontal"
          form={form}
          initialValues={initialValues}
          footer={
            <Button block color="primary" onClick={onSubmit} size="large">
              修改密码
            </Button>
          }
        >
          <Form.Item name="oldPassword" label="旧密码" arrow>
            <Input placeholder="请输入旧密码" type="password" clearable />
          </Form.Item>
          <Form.Item name="newPassword" label="新密码" arrow>
            <Input placeholder="请输入新密码" type="password" clearable />
          </Form.Item>
          <Form.Item name="confirmPassword" label="确认新密码" arrow>
            <Input placeholder="请输入再次输入密码" type="password" clearable />
          </Form.Item>
        </Form>
      </div>
    </div>
  )
}

export default ModifyPasswordMobile
